@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.customNavbar {
  font-family: 'Montserrat', sans-serif;
}

.about {
  font-family: 'Montserrat', sans-serif;
}

.recipes {
  font-family: 'Montserrat', sans-serif;
}

.buy {
  font-family: 'Montserrat', sans-serif;
}

.contact {
  font-family: 'Montserrat', sans-serif;
}

@media (min-width: 992px) { /* Bootstrap 'lg' breakpoint */
  .custom-padding-right {
    padding-right: 3rem; /* Adjust as needed */
  }
  .custom-padding-left {
    padding-left: 3rem; /* Adjust as needed */
  }
 
}

.mainTitle{
  font-size:60px;
}

@media (max-width: 768px) {
  .mainTitle{
    font-size:32px;
  }
}
html {
  scroll-behavior: smooth;
}

.home {
    background: url(/SSG_Cookbook/static/media/homeBackground.4a81c0d5.png) no-repeat center center fixed;
    background-size: cover;
    color: black; /* Ensure text color is black for better visibility */
    font-family: 'Montserrat', sans-serif;
}

.text-column {
    text-align: left; /* Aligns text to the left */
}

.text-column p {
    margin: 0; /* Removes default margin */
}

.intro-line {
    font-weight: 100;
    font-size: 2.5rem; /* 40px on default font-size of 16px */
    margin-bottom: 20px; /* Adjust spacing between lines */
}

.main-title {
    font-weight: bold;
    font-size: 4.25rem; /* 68px on default font-size of 16px */
    
}

.call-to-action {
    font-weight: 100;
    font-size: 2.6875rem; /* 43px on default font-size of 16px */
}

.image-column img {
    max-width: 100%; /* Ensures the image does not exceed its container's width */
    height: 400px; /* Fixed height for the image */
}


/* Responsive adjustments for smaller screens */
@media (max-width: 768px) {
    html {
        font-size: 14px; /* Smaller base font size for small devices */
    }

    .intro-line {
        font-size: 1.75rem; /* Adjusted size based on new base font size */
    }

    .main-title {
        font-size: 2.5rem; /* Adjusted size based on new base font size */
    }

    .call-to-action {
        font-size: 1.875rem; /* Adjusted size based on new base font size */
    }
    .image-column img {
        margin-top:-100px;
    }

}


.recipes {
    overflow: hidden; /* Ensures nothing overflows outside this component */
}

.mainTitle {
    z-index: 2; /* Ensures title stays on top */
    position: relative; /* For z-index to take effect */
    margin-bottom: 20px; /* Adds some bottom margin for spacing */
}

.recipe-image-container {
    position: relative;
    overflow: hidden;
    margin-bottom: 30px; /* Increase bottom margin for better spacing */
}

.recipe-text {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.7); /* Adjust for better readability */
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: opacity 0.3s ease;
    padding: 10px;
    box-sizing: border-box;
}

.recipe-image-container:hover .recipe-text {
    opacity: 1;
}





.paypal-steps li, .zelle-steps li {
    margin-bottom: 10px; /* Adds space between steps */
    line-height: 1.5; /* Improves readability by increasing line height */
}

.paypal-steps li:before, .zelle-steps li:before {
    margin-right: 5px;
    font-weight: bold; /* Makes 'Step' bold */
}

.cardImage {
    padding:35px;
}


.social-media-icons a {
    color: #000; /* Set the icon color to black */
    font-size: 24px; /* Increase the size of the icons */
    margin-right: 15px; /* Increase spacing between the icons */
}

.social-media-icons a:hover {
    color: #888; /* Change color on hover to a lighter shade of black (gray) */
}



.contact form .form-control {
    border: 2px solid #004F9B; /* Blue border for input fields */
    height: 48px; /* Taller input fields for better typing experience */
}

.contact form .form-label {
    font-weight: bold; /* Makes labels bold */
    color: #333; /* Darker text for better readability */
}

.contact form .btn-custom {
    background-color: #004F9B; /* Custom blue background for the button */
    color: white; /* White text for the button */
    border: none; /* No border for a cleaner look */
    padding: 10px 20px; /* Padding inside the button */
    border-radius: 5px; /* Rounded corners for the button */
    margin-top:20px;
}

.contact form .btn-custom:hover {
    background-color: #003a70; /* Darker blue on hover for the button */
}

